<template>
  <div class="wrapper">
    <video class="fullscreen" autoplay loop muted :poster="poster">
      <source :src="video" type="video/mp4">
    </video>
    <v-container class="fit">
      <transition name="custom-classes-transition" 
        :enter-active-class="left ? 'animated slideInLeft' : 'animated slideInRight'"
        :leave-active-class="left ? 'animated slideOutLeft' : 'animated slideOutRight'">
        <v-card v-if="animating" 
          :class="left ? 'mx-auto projectcard-left' : 'mx-auto projectcard-right'" 
          shaped color="rgba(0, 0, 0, 0.6)"
        >
          <v-img class="logo" :src="logo">
          </v-img>

          <v-card-text class="white--text">
            <v-sheet class="pa-2 ma-2 blank">
              <slot name="description"></slot>
            </v-sheet>

            <v-container>
              <SkillList :skills="tags" />
            </v-container>

          </v-card-text>

          <v-card-actions>
            <v-btn class="text-xs-center" color="orange" width="100%" text :to="link">
              READ MORE
            </v-btn>
          </v-card-actions>
        </v-card>
      </transition>
    </v-container>
  </div>
</template>

<script>
import SkillList from './SkillList.vue';

export default {
  props: {
    anchor: String,
    tags: Array,
    link: String,
    video: String,
    poster: String,
    logo: String,
    left: Boolean
  },
  components: {
    SkillList
  },
  computed: {

  },
  data: () => ({
    animating: false,
    large: false
  }),
  methods: {
    onResize() {
      this.large = window.innerWidth > 1200
      if (window.innerWidth > 800) {
        // Small stuff here
      }
    }
  },
  mounted() {
    // Bind resize monitor
    this.onResize()
    window.addEventListener('resize', this.onResize, { passive: true })

    // Monitor parent scrolling to this section
    this.$root.$on('onSlide', (origin, destination) => {
      if (destination.anchor === this.anchor) {

        // Check if already animating
        if (this.animating) return

        // Animate
        setTimeout((instance) => {
          instance.animating = true
        }, 200, this)

      }
    })
  },
  beforeDestroy() {
    if (typeof window === 'undefined') return
    window.removeEventListener('resize', this.onResize, { passive: true })
  }
}
</script>
<style scoped>
.wrapper {
  height: 100%;
}

.fullscreen {
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  float: left;
}

.fit {
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
}

.logo {
  height: 90%;
  width: 90%;
  top: 25px;
  margin-bottom: 20px;
  left: 5%;
}

.blank {
  background: none;
}

.projectcard-left {
  width: 80%;
  max-width: 600px;
  min-height: 400px;
  float: left;
  left: 10%;
  top: 10vh;
}

.projectcard-right {
  width: 80%;
  max-width: 600px;
  min-height: 400px;
  float: right;
  right: 10%;
  top: 10vh;
}
</style>
