<template>
  <full-page :options="options" id="fullpage" ref="fullpage">
    <div class="section">
      <Landing ref="landing"/>
    </div>
    <div class="section">
      <ProjectOathsworn ref="oathsworn" anchor="oathsworn" />
    </div>
    <div class="section">
      <ProjectSimly ref="simly" anchor="simly" />
    </div>
  </full-page>
</template>

<script>
import {FULLPAGE_LICENSE} from '@/config.js'
import Landing from '@/components/home/LandingPage.vue'
import ProjectOathsworn from '@/components/home/ProjectOathsworn.vue'
import ProjectSimly from '@/components/home/ProjectSimly.vue'
import { EventBus } from '@/main.js'

export default {
  components: {
    Landing,
    ProjectOathsworn,
    ProjectSimly
  },
  computed: {
    small () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return true
        case 'sm': return true
        case 'md': return true
        case 'lg': return false
        case 'xl': return false
        default: return true;
      }
    }
  },
  data: () => ({
    isMobile: false,
    options: {
      licenseKey: FULLPAGE_LICENSE,
      anchors: ['landing', 'oathsworn', 'simly'],
      menu: '#menu',
      navigation: true,
      navigationPosition: 'right',
      animateAnchor: true,
      recordHistory: true,
      afterLoad: function (origin, destination, direction) {
        EventBus.$root.$emit('onSlide', origin, destination, direction)
      },
      onLeave: function (origin, destination, direction) {
        EventBus.$root.$emit('onSlide', origin, destination, direction)
      }
    }
  }),
  methods: {
    onResize () {
      this.isMobile = window.innerWidth < 800
      try {
        this.$root.$emit('onMobileToggle', this.isMobile)
      } catch (ex) {
        console.log('Root not initialized yet, resize ignored.')
      }
    }
  },
  mounted () {
    this.onResize()
    window.addEventListener('resize', this.onResize, { passive: true })
  },
  beforeDestroy () {
    if (typeof window === 'undefined') return
    window.removeEventListener('resize', this.onResize, { passive: true })
  }
}
</script>

